

*{ 
    box-sizing: 0;
}

body {
    margin:0;
    font-family: Inter, sans-seriff;
    background-color:#282D35;

}


/* navbar */
.nav-bar{
    display:flex;
    align-items:center;
    background-color: #21222A;
    padding: 100px 50px;
    height: 90px;
}

.nav-icon{
    height:30px;
    margin-right: 10px;
}

.nav--title, .nav--logo_text{
    margin:0;
}


.nav--logo_text{
    font-weight: 700;
    font-size: 22px;
    margin-right: auto;
    color:#61DAFB;
}



.nav--title{
    color:#DEEBF8;
    font-weight: 600;

}


/* main  */
main{

    padding: 50px 50px;
    color:#FFFFFF;
    background: url("../images/logo192.png");
    background-repeat: no-repeat;
    background-position: 100% 75%;
    



}

.main--title{
    margin:0;
    font-size: 39px;
    letter-spacing:-0.05em;
}

.main--facts{
    margin-top: 46px;
    max-width: 370px;
}

/* affects individual li elements */
.main--facts > li{
    line-height:19px;    
    /* only does padding top and down */
    padding-block:10px; 
}

.main--facts > li::marker {
    font-size: 1.4rem;
    color:#61DAFB;

}